function punchLevel(num) {
  let arr = []
  medalPunch.forEach((e,i) =>{
    if( num >= e.number ){
      arr.push({
        ...e,
        level: i+1
      })
    }

    if(num == 0){
      arr.push({
        ...e,
        level:0
      })
    }
  })

  return arr
}

function readLevel(num) {
  let arr = []
  medalRead.forEach((e,i) =>{
    if( num >= e.number ){
      arr.push({
        ...e,
        level: i+1
      })
    }

    if(num == 0){
      arr.push({
        ...e,
        level:0
      })
    }
  })

  return arr
}

